@keyframes cycle-background {
  0% {
    background-position: 0 16.66%; }
  16.66% {
    background-position: 0 33.32%; }
  33.32% {
    background-position: 0 49.98%; }
  49.98% {
    background-position: 0 66.64%; }
  66.64% {
    background-position: 0 83.3%; }
  83.3% {
    background-position: 0 99.96%; } }

